import React, { useState, useRef, useEffect } from 'react'
import { observer, inject } from 'mobx-react'
import XButton from '../icons/x.png'
import VButton from '../icons/v.png'
import deleteBtn from '../icons/throw.png'
import question from '../icons/question.png'

const Costumer = inject("Inventory")(observer((props) => {

    const [field, setField] = useState('')
    const firstUpdate = useRef(true)

    useEffect( () => {
        if (firstUpdate.current)  firstUpdate.current = false
            else props.Inventory.editField(field, prompt('insert new field'), props.costumer._id)
      }, [field])
    

    return (

        <tr className="pure-table-odd">
            <td onClick={() => setField('name')}>{props.costumer.name}</td>
            <td onClick={() => setField('email')}>{props.costumer.email}</td>
            <td onClick={() => setField('firstContact')}>
                {
                    props.costumer.firstContact.slice(8,10)+'.'+
                    props.costumer.firstContact.slice(5,7)+'.'+
                    props.costumer.firstContact.slice(0,4)
                }
            </td>
            <td onClick={() => setField('emailType')}>
                {props.costumer.emailType&&
                props.costumer.emailType!=='null'?
                 props.costumer.emailType :
                 <img className="yesOrNo" src={question} alt="text"/>
                }
            </td>
            <td onClick={() => setField('sold')}>
                 <img className="yesOrNo" src={props.costumer.sold&&
                                               props.costumer.sold !== 'no' ?
                                                VButton : XButton} alt="text"
                 /> 
            </td>
            <td onClick={() => setField('owner')}>{props.costumer.owner}</td>
            <td onClick={() => setField('country')}>{props.costumer.country}</td>
            <td id="deleteContainer" onClick={() => props.Inventory.removeCostumer(props.costumer._id)}>
                <img className="delete" src={deleteBtn} alt="text"/>
            </td>
        </tr>

    )
}))

export default Costumer
