import React, { useState, useEffect } from 'react'
import { observer, inject } from 'mobx-react'
import { ResponsiveLine } from '@nivo/line'

const Line = inject("Inventory")(observer((props) => {
  const newLocal = 'serieColor';

  const [Data, setData] = useState([])

  useEffect( () => {
    const fetch = async()=>{
    await props.Inventory.topEmployees()
    const topEmp = props.Inventory.topEmp
    const data = [
      {
        "id": `${topEmp['2016'][0].employee}`,
        "color": "hsl(62, 70%, 50%)",
        "data": [
        {
          "x": "2016",
          "y": `${topEmp['2016'][0].costumers}`
        },
        {
          "x": "2017",
          "y": `${topEmp['2017'][0].costumers}`
        },
        {
          "x": "2018",
          "y": `${topEmp['2018'][0].costumers}`
        },
        {
          "x": "2019",
          "y": `${topEmp['2019'][0].costumers}`
        },
        {
          "x": "2020",
          "y": `${topEmp['2020'][0].costumers}`
        }
      ]
    },
    {
      "id": `${topEmp['2016'][1].employee}`,
      "color": "hsl(241, 70%, 50%)",
      "data": [
        {
          "x": "2016",
          "y": `${topEmp['2016'][1].costumers}`
        },
        {
          "x": "2017",
          "y": `${topEmp['2017'][1].costumers}`
        },
        {
          "x": "2018",
          "y": `${topEmp['2018'][1].costumers}`
        },
        {
          "x": "2019",
          "y": `${topEmp['2019'][1].costumers}`
        },
        {
          "x": "2020",
          "y": `${topEmp['2020'][1].costumers}`
        }
      ]
    },
    {
      "id": `${topEmp['2016'][2].employee}`,
      "color": "hsl(197, 70%, 50%)",
      "data": [
        {
          "x": "2016",
          "y": `${topEmp['2016'][2].costumers}`
        },
        {
          "x": "2017",
          "y": `${topEmp['2017'][2].costumers}`
        },
        {
          "x": "2018",
          "y": `${topEmp['2018'][2].costumers}`
        },
        {
          "x": "2019",
          "y": `${topEmp['2019'][2].costumers}`
        },
        {
          "x": "2020",
          "y": `${topEmp['2020'][2].costumers}`
        }
      ]
    },
    {
      "id": `${topEmp['2016'][3].employee}`,
      "color": "hsl(165, 70%, 50%)",
      "data": [
        {
          "x": "2016",
          "y": `${topEmp['2016'][3].costumers}`
        },
        {
          "x": "2017",
          "y": `${topEmp['2017'][3].costumers}`
        },
        {
          "x": "2018",
          "y": `${topEmp['2018'][3].costumers}`
        },
        {
          "x": "2019",
          "y": `${topEmp['2019'][3].costumers}`
        },
        {
          "x": "2020",
          "y": `${topEmp['2020'][3].costumers}`
        }
      ]
    },
    {
      "id": `${topEmp['2016'][4].employee}`,
      "color": "hsl(84, 70%, 50%)",
      "data": [
        {
          "x": "2016",
          "y": `${topEmp['2016'][4].costumers}`
        },
        {
          "x": "2017",
          "y": `${topEmp['2017'][4].costumers}`
        },
        {
          "x": "2018",
          "y": `${topEmp['2018'][4].costumers}`
        },
        {
          "x": "2019",
          "y": `${topEmp['2019'][4].costumers}`
        },
        {
          "x": "2020",
          "y": `${topEmp['2020'][4].costumers}`
        }
      ]
    }
  ]
  setData(data)
}
fetch()
},[])

return (
  
    <ResponsiveLine
    data={Data}
      margin={{ top: 50, right: 110, bottom: 50, left: 60 }}
      xScale={{ type: 'point' }}
      yScale={{ type: 'linear', min: 'auto', max: 'auto', stacked: true, reverse: false }}
      axisTop={null}
      axisRight={{legend: 'Owner'}}
      axisBottom={{
        orient: 'bottom',
        tickSize: 5,
        tickPadding: 5,
        tickRotation: 0,
        legend: 'year',
        legendOffset: 36,
        legendPosition: 'middle'
      }}
      axisLeft={{
        orient: 'left',
        tickSize: 5,
        tickPadding: 5,
        tickRotation: 0,
        legend: 'sales',
        legendOffset: -40,
        legendPosition: 'middle'
      }}
      colors={{ scheme: 'nivo' }}
      pointSize={10}
      pointColor={{ theme: 'background' }}
      pointBorderWidth={2}
      pointBorderColor={{ from: newLocal }}
      pointLabel="y"
      pointLabelYOffset={-2}
      useMesh={true}
      legends={[
        {
          anchor: 'bottom-right',
          direction: 'column',
          justify: false,
          translateX: 100,
          translateY: 0,
          itemsSpacing: 0,
          itemDirection: 'left-to-right',
          itemWidth: 80,
          itemHeight: 20,
          itemOpacity: 0.75,
          symbolSize: 12,
          symbolShape: 'circle',
          symbolBorderColor: 'rgba(0, 0, 0, .5)',
          effects: [
            {
              on: 'hover',
              style: {
                itemBackground: 'rgba(0, 0, 0, .03)',
                itemOpacity: 1
              }
            }
          ]
        }
      ]}
    />
  )
}))
export default Line