import React, { useState, useEffect } from 'react'
import { observer, inject } from 'mobx-react'
import globe from '../icons/globe.png'
import gmail from '../icons/gmail.png'
import client from '../icons/client.png'
import increase from '../icons/increase.png'
import axios from 'axios'

const Icon = inject("Inventory")(observer((props) => {

    const [hottest, setHottest] = useState('')

    useEffect(() => {
        async function fetch() {
            const hottest = await axios.get(`/api/hottestCountry`)
            setHottest(hottest.data)
        }
        fetch()
    }, [])
    const icons = { globe, gmail, client, increase }
    const numbers = {
        globe: hottest,
        gmail: props.Inventory.updatedNumOfEmails,
        client: props.Inventory.updatedNumOfOutstandingCostumers,
        increase: props.Inventory.updatedNumOfNewlyJoinedCostumers
    }

    return (
        <div className="icon">
            <img id="plusBtn" src={icons[props.category]} style={{ width: '15%' }} alt="text"/>
            {numbers[props.category]}
        </div>
    );
}))

export default Icon
