import React, { useState, useEffect } from 'react'

// const DropDown = inject("Inventory")(observer((props) => {

function DropDown(label, defaultState, options, props) {

    const [input, setInput] = useState('')

    // const inputPassing = name => props.getInput(name)

    const handleInput = ({ target }) => setInput(target.value)

    var [state, updateState] = React.useState(defaultState)

    var id = `use-dropdown-${label.replace(" ", "").toLowerCase()}`

    useEffect(() => {
        props.sortTable(state, input)
    }, [state])

    function Dropdown() {
        return (
            <div id="cover">
                <input
                    type="text"
                    placeholder="Search Client"
                    onChange={handleInput}
                    required
                />
                
                <label htmlFor={id}>
                    {label}
                    <select
                        id={id}
                        value={state}
                        onChange={e => updateState(e.target.value)}
                        onBlur={e => updateState(e.target.value)}
                        disabled={!options.length}
                    >
                        <option />
                        {options.map(item => (
                            <option key={item} value={item} >
                                {item}
                            </option>
                        ))}
                    </select>
                </label>
            </div>
        );
    }
    return [state, Dropdown, updateState];
};

export default DropDown;
