import React, { useState, useEffect } from 'react'
import { observer, inject } from 'mobx-react'
import { ResponsiveAreaBump } from '@nivo/bump'

const AreaBump = inject("Inventory")(observer((props) => {

  const [Data, setData] = useState([])

  useEffect( () => {
    const fetch = async()=>{
    await props.Inventory.topSalesCountry()
    const topCountry = props.Inventory.topCountry
    const data =[
      {
        "id": `${topCountry['2016'][0].country}`,
        "data": [
          {
            "x": 2016,
            "y": topCountry['2016'][0].sales
          },
          {
            "x": 2017,
            "y": topCountry['2017'][0].sales
          },
          {
            "x": 2018,
            "y": topCountry['2018'][0].sales
          },
          {
            "x": 2019,
            "y": topCountry['2019'][0].sales
          },
          {
            "x": 2020,
            "y": topCountry['2020'][0].sales
          }
        ]
      },
      {
        "id": `${topCountry['2016'][1].country}`,
        "data": [
          {
            "x": 2016,
            "y": topCountry['2016'][1].sales
          },
          {
            "x": 2017,
            "y": topCountry['2017'][1].sales
          },
          {
            "x": 2018,
            "y": topCountry['2018'][1].sales
          },
          {
            "x": 2019,
            "y": topCountry['2019'][1].sales
          },
          {
            "x": 2020,
            "y": topCountry['2020'][1].sales
          }
        ]
      },
      {
        "id": `${topCountry['2016'][2].country}`,
        "data": [
          {
            "x": 2016,
            "y": topCountry['2016'][2].sales
          },
          {
            "x": 2017,
            "y": topCountry['2017'][2].sales
          },
          {
            "x": 2018,
            "y": topCountry['2018'][2].sales
          },
          {
            "x": 2019,
            "y": topCountry['2019'][2].sales
          },
          {
            "x": 2020,
            "y": topCountry['2020'][2].sales
          }
        ]
      },
      {
        "id": `${topCountry['2016'][3].country}`,
        "data": [
          {
            "x": 2016,
            "y": topCountry['2016'][3].sales
          },
          {
            "x": 2017,
            "y": topCountry['2017'][3].sales
          },
          {
            "x": 2018,
            "y": topCountry['2018'][3].sales
          },
          {
            "x": 2019,
            "y": topCountry['2019'][3].sales
          },
          {
            "x": 2020,
            "y": topCountry['2020'][3].sales
          }
        ]
      },
      {
        "id": `${topCountry['2016'][4].country}`,
        "data": [
          {
            "x": 2016,
            "y": topCountry['2016'][4].sales
          },
          {
            "x": 2017,
            "y": topCountry['2017'][4].sales
          },
          {
            "x": 2018,
            "y": topCountry['2018'][4].sales
          },
          {
            "x": 2019,
            "y": topCountry['2019'][4].sales
          },
          {
            "x": 2020,
            "y": topCountry['2020'][4].sales
          }
        ]
      }
    ]
  setData(data)
}
fetch()
},[])

return (

    <ResponsiveAreaBump
        data={Data}
        margin={{ top: 40, right: 100, bottom: 40, left: 100 }}
        spacing={8}
        colors={{ scheme: 'nivo' }}
        blendMode="multiply"
        defs={[
            {
                id: 'dots',
                type: 'patternDots',
                background: 'inherit',
                color: '#38bcb2',
                size: 4,
                padding: 1,
                stagger: true
            },
            {
                id: 'lines',
                type: 'patternLines',
                background: 'inherit',
                color: '#eed312',
                rotation: -45,
                lineWidth: 6,
                spacing: 10
            }
        ]}
        fill={[
            {
                match: {
                    id: 'CoffeeScript'
                },
                id: 'dots'
            },
            {
                match: {
                    id: 'TypeScript'
                },
                id: 'lines'
            }
        ]}
        startLabel="id"
        axisTop={{legend: 'Sales By Country'}}
        axisBottom={{
            tickSize: 5,
            tickPadding: 5,
            tickRotation: 0,
            legend: 'year',
            legendPosition: 'middle',
            legendOffset: 32
        }}
    />
    )
  }))
  export default AreaBump
