import React, { useState, useEffect } from 'react'
import { BrowserRouter as Router } from 'react-router-dom';
import { Route } from 'react-router-dom';
import { observer, inject } from 'mobx-react'
import Navbar from './components/Navbar'
import Analytics from './components/Analytics'
import axios from 'axios'
import Home from './components/Home'
import './App.css';


const App = inject("Inventory")(observer((props) => {

  const [input, setInput] = useState('')
  const [field, setfield] = useState('')

  const sortTable = (field, input) => {
    setfield(field)
    setInput(input)
  }

  useEffect( () => {
    props.Inventory.costumers = []
    async function fetch(){
    const pulledDataFromDB = await axios.get(`/api/clients?field=${field}&input=${input}`)
    pulledDataFromDB.data.map(r => props.Inventory.costumers.unshift(r))
    }
    fetch()
  }, [field,input])

  return (
    <Router>
      <Navbar sortTable={sortTable}/>
      <Route path="/" exact component={Home} />
      <Route path="/analytics" exact render={() => <Analytics/>} />
    </Router>
  )
}))

export default App;
