import React from 'react'
import { observer, inject } from 'mobx-react'
import Doughnut from '../components/Doughnut'
import Bar from '../components/Bar'
import Icon from '../components/icon'
import AreaBump from '../components/AreaBump'
import Line from '../components/Line'

const Analytics = inject("Inventory")(observer((props) => {
 
    return (
        <div>
            <div style={{ display: 'flex' , marginTop:'1em'}}>
                <div style={{ height: '10vh', width: '25vw' }}>
                    <Icon category={'increase'}/>
                </div>
                <div style={{ height: '10vh', width: '25vw' }}>
                    <Icon category={'gmail'}/>
                </div>
                <div style={{ height: '10vh', width: '25vw' }}>
                    <Icon category={'client'}/>
                </div>
                <div style={{ height: '10vh', width: '25vw' }}>
                    <Icon category={'globe'}/>
                </div>
            </div>
            <div style={{ display: 'flex' }}>
                <div style={{ height: '35vh', width: '30vw' }}>
                    <Doughnut />
                </div>
                <div style={{ height: '35vh', width: '70vw' }}>
                    <Bar />
                </div>
            </div>
            <div style={{ display: 'flex' }}>
                <div style={{ height: '35vh', width: '50vw' }}>
                    <Line />
                </div>
                <div style={{ height: '35vh', width: '50vw' }}>
                    <AreaBump />
                </div>
            </div>
        </div>
    );
}))

export default Analytics
