import React, { useState, useEffect } from 'react'
import { observer, inject } from 'mobx-react'
import { ResponsiveBar } from '@nivo/bar'

const Bar = inject("Inventory")(observer((props) => {

    const [Data, setData] = useState([])

    useEffect(() => {
        const fetch = async () => {
            await props.Inventory.salesByLastMonth()
            const SLM = props.Inventory.SBLM
            const data = []
            SLM.map(r=> data.push(
                {
                    "date": `${r.month}`,
                    "Sales": r.sales,
                    "Salescolor": "hsl(326, 70%, 50%)"
                }
            ))
            console.log(data)
            setData(data)
        }
        fetch()
    }, [])

    return (
        <ResponsiveBar
            data={Data}
            keys={["Sales"]}
            indexBy="date"
            margin={{ top: 50, right: 130, bottom: 50, left: 60 }}
            padding={0.3}
            colors={{ scheme: 'nivo' }}
            defs={[
                {
                    id: 'dots',
                    type: 'patternDots',
                    background: 'inherit',
                    color: '#38bcb2',
                    size: 4,
                    padding: 1,
                    stagger: true
                },
                {
                    id: 'lines',
                    type: 'patternLines',
                    background: 'inherit',
                    color: '#eed312',
                    rotation: -45,
                    lineWidth: 6,
                    spacing: 10
                }
            ]}

            borderColor={{ from: 'color', modifiers: [['darker', 1.6]] }}
            axisTop={null}
            axisRight={null}
            axisBottom={{
                tickSize: 5,
                tickPadding: 5,
                tickRotation: 0,
                legend: 'Date',
                legendPosition: 'middle',
                legendOffset: 32
            }}
            axisLeft={{legend: 'Sales'}}
            labelSkipWidth={12}
            labelSkipHeight={12}
            labelTextColor={{ from: 'color', modifiers: [['darker', 1.6]] }}
            legends={[
                {
                    dataFrom: 'keys',
                    anchor: 'bottom-right',
                    direction: 'column',
                    justify: false,
                    translateX: 120,
                    translateY: 0,
                    itemsSpacing: 2,
                    itemWidth: 100,
                    itemHeight: 20,
                    itemDirection: 'left-to-right',
                    itemOpacity: 0.85,
                    symbolSize: 20,
                    effects: [
                        {
                            on: 'hover',
                            style: {
                                itemOpacity: 1
                            }
                        }
                    ]
                }
            ]}
            animate={true}
            motionStiffness={90}
            motionDamping={15}
        />
    )
}))

export default Bar