import { observable, action, computed } from 'mobx'
import data from '../store/data.json'
import axios from 'axios'
import moment from 'moment'


class Costumer {
    constructor() {
        this.state = {
            name: 'fill me',
            email: 'fill me',
            firstContact: 'fill me',
            emailType: 'fill me',
            sold: false,
            owner: 'fill me',
            country: 'fill me'
        }
    }
}

export class Inventory {

    @observable costumers = []
    @observable topEmp = {}
    @observable topCountry = {}
    @observable SBLM = []

    @computed get updatedNumOfOutstandingCostumers() {
        let counter = 0
        this.costumers.map(r => r.sold && r.sold !== 'no' ? counter++ : null)
        return counter
    }
    @computed get updatedNumOfEmails() {
        let counter = 0
        this.costumers.map(r => r.email === 'fill me' ? null : counter++)
        return counter
    }
    @computed get updatedNumOfNewlyJoinedCostumers() {
        let counter = 0
        this.costumers.map(r => r.firstContact.slice(6, 7) === moment().format('L').slice(1, 2) ? counter++ : null)
        return counter
    }
    @computed get aquisitionsByTime() {
        let lastMonth = 0
        let last6monthes = 0
        let lastYearAndAbove = 0
        for (let costumer of this.costumers) {
            if (2020 - parseInt(costumer.firstContact.slice(0, 4)) < 2 &&
                moment().subtract(1, 'months') < moment(costumer.firstContact)) lastMonth++
            else if (2020 - parseInt(costumer.firstContact.slice(0, 4)) < 2 &&
                moment().subtract(7, 'months') < moment(costumer.firstContact)) last6monthes++
            else lastYearAndAbove++
        }
        return { lastMonth, last6monthes, lastYearAndAbove }
    }

    @action addCostumer = async () => {
        const costumer = new Costumer()
        const isSavedToDB = await axios.post(`/api/client`, costumer.state)
        // this.upadtingDBwithJsonFile()
        if (isSavedToDB) this.costumers.unshift(costumer.state)
    }

    @action removeCostumer = async (id) => {
        if ((typeof id) === 'number') {
            const isDBChanged = await axios.delete(`/api/client/${id}`)
            if (!isDBChanged) return
        }
        let index = this.costumers.findIndex(i => i._id === id)
        this.costumers.splice(index, 1)
    }

    @action editField = async (field, value, id) => {
        if ((typeof id) === 'number') {
            const isDBChanged = await axios.put(`/api/client`, { field, value, id })
            if (!isDBChanged) return
        }
        let found = this.costumers.findIndex(i => i._id === id)
        this.costumers[found][field] = value
    }

    @action topEmployees = async () => {
        for (let year = 2016; year <= 2020; year++) {
            let results = await axios.get(`/api/topEmployee/${year}`)
            if(results.data.length !== 5) {
                for(let i = results.data.length; i < 5; i++)
                results.data.push({ employee: '', costumers: 0 })
            }
            this.topEmp[`${year}`] = results.data
        }
    }

    @action topSalesCountry = async () => {
        for (let year = 2016; year <= 2020; year++) {
            let results = await axios.get(`/api/topSalesCountry/${year}`)
            if(results.data.length !== 5){
                for(let i = results.data.length; i < 5; i++){
                    results.data.push({ country: 'Greece', sales: 0 })
                }
            }
            this.topCountry[`${year}`] = results.data
        }
    }

    @action salesByLastMonth = async () => {
            let results = await axios.get(`/api/salesByLastMonth`)
            this.SBLM = results.data
        }

    upadtingDBwithJsonFile = () => {
        let arr = []
        for (let costumer in data) {
            let obj = { ...data[costumer] }
            obj._id = null
            if (obj.sold === true) obj.sold = 'yes'
            if (obj.sold === false) obj.sold = 'no'
            arr.push(obj)
            if (costumer === 100) break
        }
        arr.map(r => axios.post(`/api/client`, r))
    }

}

