import React from 'react'
import { observer, inject } from 'mobx-react'
import Costumer from '../components/Costumer'
import '../App.css';

const Table = inject("Inventory")(observer((props) => {

  return (
    <div id="tableContainer">
    <table>
      <thead>
        <tr>
          <th>Name</th>
          <th>Email</th>
          <th>First Contact</th>
          <th>Email Type</th>
          <th>Sold</th>
          <th>Owner</th>
          <th>Country</th>
          <th>Remove</th>
        </tr>
      </thead>
      <tbody className="table">
        {props.Inventory.costumers.map((costumer, index) => <Costumer costumer={costumer} key={index} />)}
      </tbody>
    </table>
  </div>

      );
}))

export default Table