import React from 'react'
import { Link } from 'react-router-dom';
import { observer, inject } from 'mobx-react'
import plusButton from '../icons/add.png'
import analyticsButton from '../icons/graphic.png'
import clientsButton from '../icons/clients.png'
import DropDown from '../components/DropDown'
import '../App.css';

const Navbar = inject("Inventory")(observer((props) => {

    var names = ['sold','name','country','owner','email']
    var [names, CategoriesDropDown] = DropDown('', 'country', names, props)

    return (
        <header className='navbar'>
            <div className='navbar__title'>Super Clients Manager</div>
                <CategoriesDropDown />
            <div className='navbar__item'>
                <img id="plusBtn" src={plusButton} 
                     onClick={() => props.Inventory.addCostumer()} 
                     alt="text"
                />
            </div>
            <div className='navbar__item'>
                <Link to="/">
                    <img id="analyticsBtn" src={clientsButton} alt="text"/>
                </Link>
            </div>
            <div className='navbar__item'>
                <Link to="/analytics">
                    <img id="analyticsBtn" src={analyticsButton} alt="text" />
                </Link>
            </div>
        </header >

    );
}))

export default Navbar